import { useEffect, useState } from "react";
import { VEHICLE_TYPES } from "../../utils/information";
import { ChooseVehicleTypeCard } from "../reusable_components/create_booking/choose_vehicle_type_card";
import { useCreateBookingLocalStorage } from "../../utils/hooks/use_local_storage_createbooking";
import { LOCAL_STORAGE_KEYS } from "../../utils/local_storage_keys";
import { Link } from "react-router-dom";
import { ROUTE_LINKS } from "../../utils/route_links";

export default function ChooseVehicleType() {
  const [choosenVehicleType, setChoosenVehicleType] = useState(
    Number(1).toFixed(1)
  );
  const { setCreateBookingObj, getCreateBookingObj } =
    useCreateBookingLocalStorage(LOCAL_STORAGE_KEYS.create_booking);

  // useEffect
  useEffect(() => {
    const item = getCreateBookingObj();
    if (item) {
      setChoosenVehicleType((r) => Number(item.vehicle_type).toFixed(1));
    }
  }, []);

  // return
  return (
    <main className="max-w-xl mx-auto my-4 p-6 text-black bg-white">
      <h2 className="text-start text-black pb-6 px-2 font-extrabold text-3xl">
        Choose Vehicle type
        <p className="text-sm font-normal text-slate-600 pt-2">
          Thank you for choosing Matols logistics services as your go to service
          provider.
        </p>
      </h2>
      {VEHICLE_TYPES.map((vehicle_detail, key) => (
        <ChooseVehicleTypeCard
          key={key}
          vehicle_details={vehicle_detail}
          onChange={setChoosenVehicleType}
          value={choosenVehicleType}
        />
      ))}

      <div className="py-4 flex flex-row justify-end">
        <Link
          className="text-center font-bold px-3 py-2 rounded-md text-black bg-yellow-400 w-auto"
          reloadDocument={false}
          onClick={() =>
            setCreateBookingObj({ vehicle_type: choosenVehicleType })
          }
          to={ROUTE_LINKS.chooseroutes}
        >
          Select and continue
        </Link>
      </div>
    </main>
  );
}
